.r-container{
    overflow: auto;
    white-space: nowrap;
    padding: 10px;
    
    
}
.r-head{
    margin-bottom: 2rem;

}
.r-card{
    gap: 0.6rem;
    padding: 1rem;
    border-radius: 10px;
    
    max-width: min-content;
    margin: auto;
    transition: all 300ms ease-in ;
}
r-card>img{
    padding: 10px;

}
.r-card>:nth-child(2)
{
    font-size: 1.2rem;
    font-weight: 600;
}
.r-card>:nth-child(3)
{
    font-size: 1.5rem;
}
.r-card>:nth-child(4)
{
    font-size: 0.7rem;
    width: 15rem;
}
.r-card:hover{
    scale: 1.025;
    cursor: pointer;
    background: linear-gradient(
        180deg,
        rgba(255, 225, 225, 0)0%,
        rgba(136, 160, 255, 0.46)217.91%
    );
    box-shadow: gray;
}
.swiper-horizontal{
    overflow: visible;
}